import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Autoplay } from "swiper";
import {Swiper, SwiperSlide} from 'swiper/react';
import items from "../../assets/data/item";
import ClickMe from "../button/ClickMe";
import Modal from "../model/Model";
import imge from '../../assets/images/images/image/web.png'

const Slider = ()=> {
    const [visible, setVisible] = useState(false)

    const [datatext] = useState({
        subtitle: "工作经历",
        title: "现在我是一名前端开发的程序猿",
        desc: "在大二的时候，我加入老师工作室，开始接触前后端开发，我们有一个小团队。经过一段时间的学习，我们团队开始各司其职，我选择了后端。学习使用的是开源ruoyi框架，这也成为我毕设的一部份。",
        desc2: "大三刚结束的6月份，我进入物联网公司实习，主要负责前端开发。主要的技术栈主要有react、antd、antdpro。",
        desc3: "以下是我的项目经历\n\n" + 
        "项目一：jetlinks物联网设备管理平台\n" + 
        "      项目是一个B端项目，jetlinks(react、java)是github上的一个开源项目，我负责将公司原先使用的若依项目(vue、java)移植到jetlinks上面，用作公司项目的演示。" + 
        "工作需要调用原先项目所有api接口，需要在jetlinks上实现二次登录，获取ruoyi后台的token。jetlinks前端主要使用的是阿里的antpro、antd(3.X老版本)。最后也是成功将页面1：1的移植" + 
        "到jetlinks上。\n\n" + 
        "项目二：5G边缘网关管理平台\n" + 
        "      项目也是一个轻量级B端项目，在5G边缘网关设备中使用，主要负责物联网边缘计算，通过这个产品，我们可以配置管理各种物联设备的数据采集(例如：电表、水表、燃气等)，也可以将各种采集上来的数据通过可视化展现。" + 
        "该产品所有的配置信息都在一个json文档中，前端页面以修改json文档为主。在这里需要经常使用一些基础的算法。\n\n" + 
        "项目三：node-red开发\n" + 
        "      Node-RED一个可视化的编程工具，以满足他们快速连接硬件和设备到Web服务和其他软件的需求。使用者可以拖动node节点并进行配置，并完成数据上报。在这个过程中仍需要操作人员有一定的代码基础，" + 
        "而我的工作就是将代码操作与平台连接的代码封装成一个节点，暴露出一些简单的配置供用户使用。主要通过JavaScript实现。\n\n" + 
        "      以上项目使用的前端技术栈很少，更多的是偏向于数据结构与算法。关于其它前端知识体系，都是我通过业余时间，刷网站、面试题、看文章、以及一些短视频了解到的。目前对整个前端生态有一定的了解。"
    })

    const showModel = ()=> {
        setVisible(true)
    }

    return (
        <section className="tf-section hero-slider" id="work">
            <div className="container">
                <div className="row">
                    <div className="col-xl-5 col-md-6 col-12">
                        <div className="block-text pt-24">
                            <h6 className="gray-text sub-title mb-6" data-aos="fade-up">{datatext.subtitle}</h6>
                            <h2 className="color-text title mb-26" data-aos="fade-up">{datatext.title}</h2>
                            <p className="gray-text desc mb-42" data-aos="fade-up">{datatext.desc}</p>
                            <p className="gray-text desc mb-42" data-aos="fade-up">{datatext.desc2}</p>
                            <Link
                                to="/"
                                className="btn-action style-2"
                                data-aos="fade-up" data-aos-duration ="1200"
                                onClick={showModel}
                            >
                                职业向往
                            </Link>
                            <ClickMe/>
                        </div>
                    </div>
                    <div className="col-xl-7 col-md-6 col-12">
                        <div className="content-right d-flex">
                        <Swiper
                            modules={[ Autoplay ]}
                            direction={"vertical"}
                            spaceBetween={30}
                            slidesPerView={3}
                            loop
                            autoplay={{
                                delay: 1,
                                disableOnInteraction: false,
                                pauseOnMouseEnter: true,
                                reverseDirection: true,
                            }}
                            speed={3000}
                        >
                            <SwiperSlide><div className="item bg"><img src={items.item1} alt="" /></div></SwiperSlide>
                            <SwiperSlide><div className="item bg-2"><img src={items.item2} alt="" /></div></SwiperSlide>
                            <SwiperSlide><div className="item bg"><img src={items.item1} alt="" /></div></SwiperSlide>
                            <SwiperSlide><div className="item bg-1"><img src={items.item2} alt="" /></div></SwiperSlide> 
                        </Swiper>
                        <Swiper
                            modules={[ Autoplay ]}
                            direction={"vertical"}
                            spaceBetween={30}
                            slidesPerView={3}
                            loop
                            autoplay={{
                                delay: 1,
                                reverseDirection: true,
                                disableOnInteraction: false,
                                pauseOnMouseEnter: true,
                            }}
                            speed={2000}
                        >
                            <SwiperSlide><div className="item bg-2"><img src={items.item3} alt="" /></div></SwiperSlide>
                            <SwiperSlide><div className="item bg-3"><img src={items.item4} alt="" /></div></SwiperSlide>
                            <SwiperSlide><div className="item bg-2"><img src={items.item5} alt="" /></div></SwiperSlide>
                            <SwiperSlide><div className="item bg-1"><img src={items.item3} alt="" /></div></SwiperSlide> 
                            <SwiperSlide><div className="item bg-3"><img src={items.item4} alt="" /></div></SwiperSlide> 
                        </Swiper>
                        <Swiper
                            modules={[ Autoplay ]}
                            direction={"vertical"}
                            spaceBetween={30}
                            slidesPerView={3}
                            loop
                            autoplay={{
                                delay: 1,
                                disableOnInteraction: false,
                                reverseDirection: true,
                                pauseOnMouseEnter: true,
                            }}
                            speed={2500}
                        >
                            <SwiperSlide><div className="item bg-2"><img src={items.item6} alt="" /></div></SwiperSlide>
                            <SwiperSlide><div className="item bg-1"><img src={items.item7} alt="" /></div></SwiperSlide>
                            <SwiperSlide><div className="item bg-2"><img src={items.item4} alt="" /></div></SwiperSlide>
                            <SwiperSlide><div className="item bg-1"><img src={items.item6} alt="" /></div></SwiperSlide> 
                            <SwiperSlide><div className="item bg-2"><img src={items.item7} alt="" /></div></SwiperSlide>
                        </Swiper>
                        </div>
                    </div>
                </div>
            </div>
            <Modal close={()=>{
            setVisible(false)
        }} visible={visible} title={"项目简介"} data={datatext.desc3} imge={imge}/>
        </section>
    )
}
export default Slider;
