import React from "react";
import Header from "../components/header/header";
import Slider from "../components/slider/Slider";
import About from "../components/layouts/About";
import Speciality from "../components/layouts/Speciality";
import dataCard from '../assets/data/data-card';
import Portfolio from "../components/layouts/Portfolio";
import dataPortfolio from '../assets/data/porfolioData'
// import Monteno from "../components/layouts/Montono";
// import dataMontono from "../assets/data/data-monton";
import Roadmap from "../components/layouts/Roadmap";
import roadmapData from "../assets/data/data-roadmap";
// import NewsLetter from "../components/layouts/Newsletter";
// import Team from "../components/layouts/Team";
// import dataTeam from "../assets/data/data-team";
import Partners from "../components/layouts/Partners";
import dataPartners from '../assets/data/data-partners';
import Testimonials from "../components/layouts/Testimonials";
import dataTestimonials from "../assets/data/data-testimonials";
import FAQ from "../components/layouts/FAQ";
import dataFaq from "../assets/data/data-faq";
import Action from "../components/layouts/Action";
import Footer from "../components/footer/footer";
import Graduation from "../components/layouts/Graduation";
import dataGraduation from "../assets/data/data-graduation";
const Home = () => {
  return (
    <div>
      <Header />
      <About/>
      <Slider/>
      <Speciality data={dataCard}/>
      <Portfolio data={dataPortfolio}/>
      {/* <Monteno data={dataMontono}/> */}
      <Roadmap data={roadmapData}/>
      {/* <NewsLetter/> */}
      {/* <Team  data={dataTeam}/> */}
      <Partners data={dataPartners}/>
      <Testimonials data={dataTestimonials}/>
      <Graduation data={dataGraduation}/>
      <FAQ data={dataFaq}/>
      <Action/>
      <Footer/>
    </div>
  );
};

export default Home;
