import React from "react";
import { useState } from "react";
// import { Link } from "react-router-dom";
const Action = ()=> {
    const [data] = useState({
        title: '我相信',
        desc: '一个优秀的人，不一定是比别人更幸运，他们只是在任何一件小事上都对自己有所要求，不因舒适而散漫放纵，不因辛苦而放弃追求。雕塑自己的过程，必定伴随着疼痛与辛苦，可那一锤一凿的自我敲打，终究能让我们收获一个更好的自己。'
    })
    return (
        <section className="tf-section action">
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        <div className="action_body" data-aos="fade-up">
                            <div className="block-text">
                                <h3 className="mb-13">{data.title}</h3>
                                <p className="fs-21 mb-27">{data.desc}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}
export default Action;


