import React from "react";
import { Link } from "react-router-dom";

const Partners = (props)=> {
    const data = props.data
    return (
        <section className="tf-section partners" id="knowledge">
    <div className="container">
        <div className="row">
            <div className="col-12">
                <div className="block-text center">
                    <h1 className="color-text heading-bg" data-aos="fade-in" data-aos-duration="1000"><span>Partners</span></h1>
                    <h5 className="color-text sub-title mb-10"  data-aos="fade-up" data-aos-duration="1000">Partners</h5>
                    <h3 className="color-text title mb-28"  data-aos="fade-up" data-aos-duration="1000">下面是我使用过的 <br />一些第三方库和工具</h3>
                </div>
            </div>
        </div>
        <div className="row">
            <div className="col-12">
                <div className="partners_body" data-aos="fade-up">
                    {
                        data.map((data, index)=> (
                            <div className={`box-partner border-r ${data.border}`} key={index}>
                                <Link to={"/"}> <img src={data.img} alt="" style={{height: 38}}/>{data.title}</Link>
                            </div>
                        ))
                    }
                </div>
            </div>
        </div>
    </div>
</section>
    )
}


export default Partners;





