import React, { useState } from "react";
import { Link } from "react-router-dom";
import items from "../../assets/data/item";
// import personImage from "../../assets/images/images/image/个人照片.png";
import ClickMe from "../button/ClickMe";
import Modal from "../model/Model";
import imge from '../../assets/images/images/image/个人照片.png'


const About = ()=> {
    const [visible, setVisible] = useState(false)
    const [data] = useState({
        subtitle: "关于我",
        title: "面试官，您好，我叫施晨烨，很高兴见到您！",
        desc1: "1998年9月，我出生在浙江省金华市磐安县的某一座小山村。",
        desc2: "2023年6月，我将从宁波财经学院金融与信息学院毕业，我的专业是数据科学与大数据技术。",
        desc3: "2017年高考结束，考入宁波财经学院(全日制本科)，同年应征入伍(很幸运，就在宁波)，2019年退役复学，由原先报考的计算机" + 
        "专业转为数据科学与大数据技术，曾担任实践部部长。2021年12月，加入中国共产党。我热爱计算机，" + 
        "喜欢编程。获得过校奖学金、省政府奖学金，参加过“中国软件杯”大学生软件设计大赛、浙江省证券投资竞赛、天翼物联网开发者大赛(企业)等比赛，均获得良好"+
        "的比赛成绩。\n" +
        "       虽然高考没能考上更好的学校，但是却找到了自己喜欢的方向。编程是我的爱好。我喜欢用技术来解决问题，并花费很多时间学习技能，因为我觉得这是我未来职业生涯的方向。" + 
        "我对前端开发充满了热情，因为它是一门动态的技术，不断推出新功能和工具。我喜欢用最新的技术来开发网站和应用程序，让用户获得最佳的体验。" + 
        "我享受用代码来创造东西，特别是在前端开发中。我喜欢设计和实现用户界面，并将自己的创意和想法变成现实" + 
        "前端开发对我来说不只是一项工作或技能，更是一种激情和追求。我希望能够通过我的编程技能，为用户提供最好的网站和应用程序。"

    })

    const showModel = ()=> {
        setVisible(true)
    }

    return <section className="tf-section section-about" id="resume">
        <div className="container">
            <div className="row reverse">
                <div className="col-xl-7 col-md-12">
                    <div className="group-image">
                        <div className="left">
                            <div className="item bg-1 bs"><img src={items.item8} alt="" /></div>
                        </div>
                        <div className="right">
                            <div className="item bg-2 bs"><img src={items.item9} alt="" /></div>
                            <div className="item bg-3 bs"><img src={items.item10} alt="" /></div>
                        </div>
                    </div>
                </div>
                <div className="col-xl-5 col-md-12">
                    <div className="block-text pt-12">
                        <h5 className="gray-text sub-title mb-10" data-aos-duration="1000">{data.subtitle}</h5>
                        <h3 className="color-text title mb-58" data-aos-duration="1000">{data.title}</h3>
                        <p className="gray-text fs-21 mb-33" data-aos-duration="1000" data-aos="fade-up">{data.desc1}</p>
                        <p className="gray-text fs-18 line-h17 mb-41" data-aos-duration="1000" data-aos="fade-up">{data.desc2}</p>
                        <Link to= "/" className="btn-action style-2" data-aos-duration="1200" data-aos="fade-up" onClick={showModel}>更多关于我</Link>
                        <ClickMe/>
                    </div>
                </div>
            </div>
        </div>
        <Modal close={()=>{
            setVisible(false)
        }} visible={visible} title={"简介"} data={data.desc3} imge={imge}/>
    </section>
}

export default About;

