/* eslint-disable jsx-a11y/alt-text */
import React, {useState, useEffect, useRef} from 'react';

import './Model.css';



function handler(e) {  e.preventDefault();}
function Modal(props) {
    const {visible, close, data, title, imge} = props;
    const [filter, setFilter] = useState(0);
    const [outAction, setOutAction] = useState("")
    const Timer = useRef(null)
    useEffect(()=>{
        if(visible){
            document.addEventListener('touchmove', handler, { passive: false })
            Timer.current = setInterval(()=>{
                setFilter(filter => {
                    if(filter < 100) return filter+=1
                    else clearInterval(Timer.current)
                    return 100
                })
            },10)
            document.body.style.overflow = 'hidden';
        }else document.body.style.overflow = 'auto'
        return ()=>{
            clearInterval(Timer.current)
            document.removeEventListener('touchmove', handler, { passive: false })
            setFilter(0)
        }
    },[visible])

    const handlerFatherClick = (e)=> {
        if(e.target.className.trim() === 'modal-body-out' || e.target.className.trim()==="elements-span"){
            setOutAction("modal-body-close")
            Timer.current = setTimeout(()=>{
                close()
                setOutAction("")
                clearTimeout(Timer.current)
            },700) 

        }
    }

  return <>
    {
        visible && (
            <div className={`modal-body-out  ${outAction}`} style={{backdropFilter: `blur(${filter}px)`}}
                onClick={handlerFatherClick}
            >
                <div className='model-body'>
                <img className="elements bg" src={imge} />
                <span className='elements-span' onClick={handlerFatherClick}></span>
                <div className='model-body-text'>
                    <span>{title}</span>
                    <span>{data}</span>
                </div>
            </div>
            </div>
        )
    }
  </>;
}

export default Modal;