import React, { useState } from "react";
import { Link } from "react-router-dom";


const Speciality = (props)=> {

    const [datatext] = useState({
        subtitle: "学习",
        title: "我经常学习使用的网站",
        desc: "除了这些网站，我还会通过B站、慕课网、语雀、抖音等渠道学习。我想为一名前端架构师，想成为一名全栈工程师，开发桌面应用、手机app、移动小程序等等。"
    })

    const data = props.data;


    return<>
    <section className="tf-section section-speciality">
        <div className="container">
            <div className="row">
            <div className="col-xl-7 col-md-12">
                    <div className="section-speciality_box">
                        {
                            data.map((data, index) => (
                                <div className={`box-item bg-2 bs ${data.class}`} key={index} data-aos="flip-left">
                                    <h3 className="color-main">{data.stt}</h3>
                                    <Link to="/" className="h5">{data.title}</Link>
                                    <p>{data.desc}</p>
                                </div>
                            ))
                        }
                    </div>
                </div>
                <div className="col-xl-5 col-md-12">
                    <div className="block-text pt-128 pd-0">
                        <h5 className="gray-text sub-title mb-10" data-aos="fade-up" data-aos-duration="1000">{datatext.subtitle}</h5>
                        <h3 className="color-text title mb-28" data-aos="fade-up" data-aos-duration="1000"> {datatext.title}</h3>
                        <p className="gray-text fs-21" data-aos="fade-up" data-aos-duration="1000">{datatext.desc}</p>
                    </div>
                </div>
                
            </div>
        </div>
    </section>
    </>
}

export default Speciality;