import React from "react";
import { Link } from "react-router-dom";



const Portfolio = (props)=> {
    const data = props.data
    return <section className="tf-section portfolio" id="technical">
        <div className="container">
            <div className="row">
                <div className="cik-md-12">
                    <div className="block-text center">
                        <h1 className="color-text heading-bg" data-aos="fade-in" data-aos-duration="1000"><span>technical ability</span></h1>
                        <h5 className="color-text sub-title mb-10" data-aos="fade-up" data-aos-duration="1000">level</h5>
                        <h3 className="color-text title mb-28"  data-aos="fade-up" data-aos-duration="1000">自我评定技术 <br />情况</h3>
                    </div>
                </div>
            </div>
            <div className="row mt-61">
                {
                    data.map((data, index)=>(
                        <div className="col-md-6" key={index}>
                            <div className="icon-box bg-2 bs" data-aos="fade-up">
                                <div className="icon">
                                    <img src={data.img} alt="" style={{width: 50}}/>
                                </div>
                                <div className="content">
                                    <p className="fs-16 color-main">{data.step}</p>
                                    <Link to="/" className="h5">{data.title}</Link>
                                    <p className="fs-18">{data.desc}</p>
                                </div>
                            </div>
                        </div>
                    ))
                }
            </div>
        </div>
    </section>
}


export default Portfolio