import React from "react";
import imge from '../../assets/images/images/image/click.png'

const ClickMe = ()=> {
    return (
        // eslint-disable-next-line jsx-a11y/alt-text
            <img src={imge} alt="Chiny" style={{height:100, transform: "translateX(166%)",display: 'block'}}  data-aos="fade-left" data-aos-duration="4000"/>
      
    )
}

export default ClickMe;